<template>
  <div id="login">
    <div class="me-login-box me-login-box-radius">
      <h1>给我留言</h1>
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="是否匿名">
          <el-switch v-model="form.anonymous"></el-switch>
        </el-form-item>
        <el-form-item label="姓名" required="false">
          <el-input v-model="form.name" placeholder="姓名"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" required="true">
          <el-input v-model="form.email" placeholder="邮箱"></el-input>
        </el-form-item>
        <el-form-item label="地址" required="false">
          <el-input v-model="form.address" placeholder="地址"></el-input>
        </el-form-item>
        <el-form-item label="留言" required="true">
          <el-input type="textarea" v-model="form.message" placeholder="说点什么"></el-input>
        </el-form-item>
        <el-form-item label="其他" required="false">
          <el-input type="textarea" v-model="form.other" placeholder="Other"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">立即创建</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        form: {
          name: '',
          email: '',
          address: '',
          message: '',
          anonymous: false,
          other: ''
        }
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      }
    }
  }
</script>



